import React, { useState } from 'react';
import addToMailchimp from 'gatsby-plugin-mailchimp';

const SubscribeEmail = () => {
  const [ hasSubscribed, setHasSubscribed ] = useState(false);
  const [ hasError, setHasError ] = useState(false);
  const [ email, setEmail ] = useState('');

  const _handleSubmit = async (e) => {
    e.preventDefault();
    setHasError(false);
    const data = await addToMailchimp(email);
    console.log(data);
    if (data.result === 'error') {
      setHasError(true);
    } else if (data.result === 'success') {
      setHasSubscribed(true);
    }
  };

  return (
    <div
      style={{
        textAlign: 'center',
        padding: '1em',
        background: '#f4f4f4',
        borderRadius: '5px',
      }}
    >
      <div style={{ fontWeight: 700, marginBottom: '10px' }}>Enjoyed this post? Receive the next one in your inbox!</div>
      <form
        onSubmit={_handleSubmit}
      >
        { !hasSubscribed && (
          <div
            style={{
              display: 'flex',
              justifyContent: 'center',
            }}
          >
            <input
              type='email'
              value={email}
              onChange={e => setEmail(e.target.value)}
              placeholder='Your email address'
              style={{
                border: '1px solid black',
                borderRight: 0,
                borderRadius: '5px 0 0 5px',
                padding: '5px 10px',
                fontSize: '14px',
                minWidth: '200px',
                background: 'none',
              }}
            />
            <button
              type='submit'
              style={{
                border: '1px solid black',
                borderLeft: 0,
                borderRadius: '0 5px 5px 0',
                padding: '5px 10px',
                background: 'none',
                WebkitAppearance: 'none',
                cursor: 'pointer',
                fontSize: '14px',
              }}
            >
              Subscribe
            </button>
          </div>
        ) }
        { hasSubscribed && (
          <div>
            Hooray! You're now subscribed! <span role='img' aria-label='tada'>🎉</span>
          </div>
        ) }
        { hasError && (
          <div
            style={{
              color: 'red',
              fontSize: '12px',
              textAlign: 'center',
            }}
          >
            There's an issue subscribing this email. Could you try another one? Or try later.
          </div>
        ) }
      </form>
    </div>
  );
};

export default SubscribeEmail;
